<template>
  <div class="home" style="height: 100%">
    <pic-editer :wprops="wprops" v-if="show"></pic-editer>
    <div v-else style="height: 100%;width: 100%">
      画布宽：<input v-model="w">px&nbsp;&nbsp;&nbsp;画布高：<input v-model="h">px&nbsp;&nbsp;&nbsp;<button @click="create">创建</button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import PicEditer from "../components/PicEditer";
export default {
  name: 'home',
  components: {
    PicEditer
  },
  data() {
    return {
      show: false,
      wprops: {
        detail: {height: 0, width: 0},
        src: null
      },
      h: 768,
      w: 1280,
    }
  },
  methods: {
    create() {
      let c = document.createElement("canvas");
      c.width = this.w;
      c.height = this.h;
      let ctx = c.getContext("2d");
      ctx.fillStyle = "#FFF";
      ctx.fillRect(0,0,this.w,this.h);
      for (let i = 0; i < this.h;i += 5) {
        console.log(i,(i % 10) ? ( (i % 50) ? 5 : 15) : 8)
        ctx.beginPath();
        ctx.strokeStyle = '#000';
        ctx.lineWidth = 1;
        ctx.moveTo(0, i);
        ctx.lineTo((i % 10) ? 5 : ( (i % 50) ? 8 : 15), i);
        ctx.stroke();
        ctx.closePath();
      }
      for (let i = 0; i < this.w;i += 5) {
        console.log(i,(i % 10) ? ( (i % 50) ? 5 : 15) : 8)
        ctx.beginPath();
        ctx.strokeStyle = '#000';
        ctx.lineWidth = 1;
        ctx.moveTo(i, 0);
        ctx.lineTo(i, (i % 10) ? 5 : ( (i % 50) ? 8 : 15));
        ctx.stroke();
        ctx.closePath();
      }
      let src = c.toDataURL();
      this.wprops.src = src;
      this.wprops.detail.height = this.h;
      this.wprops.detail.width = this.w;
      this.show = true;
    }
  }
}
</script>
