<template>
  <div class="pic-edit not-screenshots">
    <!--  侧面板  -->
    <div class="pic-edit-panel" style="height: 100%;">

      <!--    工具按钮  -->
      <div class="pic-edit-panel-tools">
        <span @click="tool = el"
              :class="'pic-edit-panel-tool' + (el.key === tool.key ? ' pic-edit-panel-tool-active' : '')"
              v-for="el in toolList" :title="el.name" v-bind:key="'tools-'+el.id">
          <i :class="'fa fa-' + el.icon"></i>
        </span>
      </div>
      <!--    撤销重做  -->
      <div class="pic-edit-panel-history">
        <div class="pic-edit-panel-tools" style="height: auto">
          <span class="pic-edit-panel-tool" @click="prev" title="撤销">
            <i class="fa fa-rotate-left"></i>
          </span>
          <span class="pic-edit-panel-tool" @click="next" title="重做">
            <i class="fa fa-repeat"></i>
          </span>
          <span class="pic-edit-panel-tool" title="复制编辑后的图片" @click="save" style="background-color: rgba(226,88,104,0.3);color: #FFF">
            <i class="fa fa-save"></i>
          </span>
        </div>
        <!--        历史记录-->
        <div style="flex: 1;overflow: hidden;padding: 5px 0 10px 0">
          <div style="height: 20px">历史记录</div>
          <div style="height: calc(100% - 20px);overflow-y: auto;" ref="history_list">
            <div class="history-item" v-for="(h,i) in historyBefor.stack" v-bind:key="'history-befor-'+i">{{h.name}}
            </div>
            <div class="history-item history-item-active">{{history ? history.name : '无历史记录'}}</div>
            <div class="history-item" v-for="(h,i) in afterHistory" v-bind:key="'history-after-'+i">{{h.name}}</div>
          </div>
        </div>
      </div>
      <!--    设置面板  -->
      <div class="pic-edit-panel-option">
        <div style="margin: 5px;">
          <span class="input-title" style="width: 100%">缩放:{{zoom}}%</span>
          <input type="range" max="100" min="1" style="width: 100%" step="0.1" v-model="zoom">
        </div>
        <div style="margin: 5px;">
          <span class="input-title" style="width: 100%">画笔:{{pencil}}px</span>
          <input type="range" max="100" min="1" style="width: 100%" step="1" v-model="pencil">
        </div>
        <div style="display: none">
          <input type="color" v-model="color" ref="color_selecter">
        </div>
        <div style="padding: 5px 5px 0;">
          <div :style="colorSelecterStyle" @click="$refs.color_selecter.click()"></div>
        </div>
        <div style="padding: 5px">
          <div v-for="el in colorList" v-bind:key="el" @click="color = el"
               :class="'color-list-item' + (color === el ? ' color-list-item-active' : '')"
               :style="{backgroundColor: el}"></div>
        </div>
        <div style="padding: 5px">
          <div style="height: 22px"><span class="input-title">保留历史记录</span></div>
          <div style="height: 22px">
            <input v-model.lazy.number="historyCount" title="条" placeholder="条"
                   style="width: 60px;text-align: center;"/>
            <span class="input-title">条</span>
          </div>
        </div>
      </div>
    </div>
    <!--  绘图部分  -->
    <div class="pic-edit-container" ref="pic_edit_canvas" style="height: 100%">
      <div class="pic-edit-canvas" ref="pic_canvas_container" :style="picEditCanvasStyle"
           v-on:click="mouseclick"
           v-on:mousedown="mousedown"
           v-on:mouseup="mouseup"
           v-on:mousemove="mousemove"
           v-on:mousewheel="mousewheel">
        <canvas ref="pic_canvas" id="pic_canvas" :width="wprops.detail.width" :height="wprops.detail.height"
                v-on:mousedown="mousedownFromCanvas"
                v-on:mouseup="mouseupFromCanvas"
                v-on:mousemove="mousemoveFromCanvas"
                :style="canvasStyle"></canvas>
        <div></div>
      </div>
    </div>
  </div>
</template>

<script>

  import {Stack} from "../Stack";

  const TOOL_LIST = [
    {id: 0, key: "move", name: "移动画布", icon: "hand-paper-o", mouse: 'grab'},
    {id: 1, key: "minus", name: "缩小画布", icon: "search-minus", mouse: 'zoom-out'},
    {id: 2, key: "plus", name: "放大画布", icon: "search-plus", mouse: 'zoom-in'},
    {id: 3, key: "hLine", name: "横直线", icon: "arrows-h", mouse: 'crosshair'},
    {id: 4, key: "vLine", name: "竖直线", icon: "arrows-v", mouse: 'crosshair'},
    {id: 5, key: "line", name: "直线", icon: "minus", mouse: 'crosshair'},
    {id: 6, key: "pen", name: "画笔", icon: "pencil", mouse: 'crosshair'},
    {id: 7, key: "rect", name: "矩形", icon: "square-o", mouse: 'crosshair'},
    {id: 8, key: "circle", name: "圆", icon: "circle-o", mouse: 'crosshair'},
  ];

  class PicDo {
    constructor(dataUrl, name) {
      this.dataUrl = dataUrl;
      this.name = name;
    }
  }

  export default {
    name: "pic-editer",
    props: ["wprops"],
    computed: {
      canvasStyle() {
        let height = this.zoom / 100 * this.wprops.detail.height;
        let width = this.zoom / 100 * this.wprops.detail.width;
        this.$nextTick(() => {
          this.move(this.info.move.x, this.info.move.y)
        });
        let opt = {
          height: height + "px",
          width: width + "px",
        };
        this.transition && (opt.transition = this.transition);
        // this.transition = null;
        return opt;
      },
      colorSelecterStyle() {
        return {
          backgroundColor: this.color,
          height: '24px',
          transition: 'all 400ms'
        };
      },
      picEditCanvasStyle() {
        return {
          width: "100%",
          height: "100%",
          cursor: this.tool.mouse
        }
      },
      afterHistory() {
        return [...this.historyAfter.stack].reverse();
      }
    },
    watch: {
      "pencil": function () {
        if (this.pencil > 100) {
          this.pencil = 100;
        } else if (this.pencil < 1) {
          this.pencil = 1;
        }
      }
    },
    data() {
      return {
        historyCount: 50,
        historyBefor: new Stack(),
        historyAfter: new Stack(),
        history: null,
        toolList: TOOL_LIST,
        colorList: ["#000000", "#FFFFFF", "#33B1FF", "#00EFFF", "#FF4C00", "#FF0005", "#FFD800", "#39BB00"],
        color: "#000000",
        zoom: 1,
        pencil: 1,
        tool: TOOL_LIST[0],
        width: '100%',
        height: '100%',
        lastPoint: null,
        point: null,
        pointFromCanvas: null,
        info: {
          move: {x: 50, y: 50},
          tool: null,
          keyDown: false,
        },
        transition: null,
        timeout: {
          zoom: null
        }
      }
    },
    mounted() {
      this.resize();
      this.loadPic(this.wprops.src, () => {
        this.setHistory("打开图片");
      });
      this.suitable();

      window.addEventListener("keydown",this.keydown);
      window.addEventListener("keyup",this.keyup);

    },
    destroyed () {
      window.removeEventListener("keydown",this.keydown);
      window.removeEventListener("keyup",this.keyup);

      this.history = null;
      this.historyAfter = null;
      this.historyBefor = null;
      this.tool = null;
      this.info = null;

    },
    methods: {

      save () {
        alert("请在画布上右键复制图片")
      },

      keydown (e) {
        switch (e.code) {
          case "Space":
            if (!this.info.keyDown) {
              this.info.keyDown = true;
              this.$set(this.info,"tool",this.tool);
              this.$set(this,"tool",TOOL_LIST[0]);
            }
            break;
          case "Minus":
            if (e.ctrlKey) {
              this.zooma(this.zoom * 1 - 10);
            }
          break;
          case "Equal":
            if (e.ctrlKey) {
              this.zooma(this.zoom * 1 + 10);
            }
          break;
        };

        if (e.ctrlKey && /Numpad[0-9]/gi.test(e.code)) {
          const key = e.key * 1 - 1;
          this.$set(this,"tool",TOOL_LIST[key]);
        }
        if (e.ctrlKey && !e.shiftKey && e.code === "KeyZ") {
          this.prev();
        }
        if (e.ctrlKey && e.shiftKey && e.code === "KeyZ") {
          this.next();
        }

      },

      keyup (e) {
        switch (e.code) {
          case "Space":
            if (this.info.keyDown) {
              this.$set(this,"tool",this.info.tool);
              this.$set(this.info,"tool",null);
              this.info.keyDown = false;
            }
            break;
        }
      },

      getDataUrl() {
        let canvas = document.querySelector("#pic_canvas");
        return canvas.toDataURL();
      },

      getImageData() {
        let canvas = document.querySelector("#pic_canvas");
        let context = canvas.getContext('2d');
        return context.getImageData(0, 0, context.canvas.width, context.canvas.height);
      },

      setHistory(name) {
        this.history = new PicDo(this.getImageData(), name);
      },

      newHistory(name) {
        this.historyBefor.push(this.history);
        while (this.historyBefor.stack.length > this.historyCount) {
          this.historyBefor.reverse = true;
          this.historyBefor.pop();
          this.historyBefor.reverse = false;
        }
        this.historyAfter = new Stack();
        this.history = new PicDo(this.getImageData(), name);
        this.historyListScroll();
      },

      prevHistory() {
        const h = this.historyBefor.pop();
        if (h) {
          this.historyAfter.push(this.history);
          while (this.historyAfter.stack.length > this.historyCount) {
            this.historyAfter.reverse = true;
            this.historyAfter.pop();
            this.historyAfter.reverse = false;
          }
          this.history = h;
        }
        return this.history;
      },

      nextHistory() {
        const h = this.historyAfter.pop();
        if (h) {
          this.historyBefor.push(this.history);
          while (this.historyBefor.stack.length > this.historyCount) {
            this.historyBefor.reverse = true;
            this.historyBefor.pop();
            this.historyBefor.reverse = false;
          }
          this.history = h;
        }
        return this.history;
      },

      next() {
        const h = this.nextHistory();
        this.putImage(h.dataUrl,0,0);
      },

      prev() {
        const h = this.prevHistory();
        this.putImage(h.dataUrl,0,0);
      },

      historyListScroll() {
        this.$nextTick(() => {
          const el = this.$refs.history_list;
          el && (el.scrollTop = el.scrollHeight);
        });
      },

      resize() {
        let container = this.$refs.pic_canvas_container;
        this.width = window.getComputedStyle(container)["width"];
        this.height = window.getComputedStyle(container)["height"];
      },

      mouseclick(e) {
        switch (this.tool.key) {
          case "plus":
            this.zooma(this.zoom * 1 + 10);
            break;
          case "minus":
            this.zooma(this.zoom * 1 - 10);
            break;
        }
      },

      mousedown(e) {
        this.point = {};
        this.point.x = e.offsetX;
        this.point.y = e.offsetY;
      },

      mouseup(e) {
        this.point = null;
      },

      mousemove(e) {
        if (this.point) {
          const diff = {
            x: e.movementX,
            y: e.movementY,
          };
          const width = this.width.replace(/px/gi, "") * 1;
          const height = this.height.replace(/px/gi, "") * 1;

          this.point.x = e.offsetX;
          this.point.y = e.offsetY;

          switch (this.tool.key) {
            case "move":
              const x = this.info.move.x + diff.x / width * 100;
              const y = this.info.move.y + diff.y / height * 100;
              this.move(x, y);
              break;
          }
        }
      },

      mousedownFromCanvas(e) {
        this.pointFromCanvas = {};
        this.pointFromCanvas.x = e.offsetX;
        this.pointFromCanvas.y = e.offsetY;

        const zoom = this.zoom / 100;
        const p1 = {
          x: this.pointFromCanvas.x / zoom,
          y: this.pointFromCanvas.y / zoom
        };


        switch (this.tool.key) {
          case "pen":
            this.startDrawPath(p1);
            break;
        }
      },

      mouseupFromCanvas(e) {
        switch (this.tool.key) {
          case "hLine":
            this.putImage(this.history.dataUrl, 0, 0);
            this.hLine(this.pointFromCanvas, e.offsetX);
            this.newHistory("绘制横直线");
            break;
          case "vLine":
            this.putImage(this.history.dataUrl, 0, 0);
            this.vLine(this.pointFromCanvas, e.offsetY);
            this.newHistory("绘制竖直线");
            break;
          case "line":
            this.putImage(this.history.dataUrl, 0, 0);
            this.line(this.pointFromCanvas, {x: e.offsetX, y: e.offsetY});
            this.newHistory("绘制直线");
            break;
          case "pen":
            this.endDrawPath();
            this.newHistory("自由绘制");
            break;
          case "rect":
            this.putImage(this.history.dataUrl, 0, 0);
            this.rect(this.pointFromCanvas, {x: e.offsetX, y: e.offsetY});
            this.newHistory("绘制矩形");
            break;
          case "circle":
            this.putImage(this.history.dataUrl, 0, 0);
            this.ellipse(this.pointFromCanvas, {x: e.offsetX, y: e.offsetY});
            this.newHistory("绘制圆形");
            break;
        }
        this.endDrawPath();
        this.pointFromCanvas = null;
      },

      mousemoveFromCanvas(e) {
        if (this.pointFromCanvas) {
          switch (this.tool.key) {
            case "hLine":
              this.putImage(this.history.dataUrl, 0, 0);
              this.hLine(this.pointFromCanvas, e.offsetX);
              break;
            case "vLine":
              this.putImage(this.history.dataUrl, 0, 0);
              this.vLine(this.pointFromCanvas, e.offsetY);
              break;
            case "line":
              this.putImage(this.history.dataUrl, 0, 0);
              this.line(this.pointFromCanvas, {x: e.offsetX, y: e.offsetY});
              break;
            case "pen":
              this.pen({x: e.offsetX, y: e.offsetY});
              break;
            case "rect":
              this.putImage(this.history.dataUrl, 0, 0);
              this.rect(this.pointFromCanvas, {x: e.offsetX, y: e.offsetY});
              break;
            case "circle":
              this.putImage(this.history.dataUrl, 0, 0);
              this.ellipse(this.pointFromCanvas, {x: e.offsetX, y: e.offsetY});
              break;
          }
        }
      },

      mousewheel(e) {
        const delta = e.wheelDelta;
        if (e.ctrlKey) {
          this.pencil = this.pencil * 1 + (delta > 0 ? 2 : -2);
        } else {
          const val = this.zoom * 1 + (delta > 0 ? 2 : -2);
          this.zooma(val);
        }
        e.preventDefault ? e.preventDefault() : (window.event.returnValue == false);
      },

      loadPic(dataUrl, callback) {
        let canvas = document.querySelector("#pic_canvas");
        let ctx = canvas.getContext('2d');
        let img = new Image();
        img.onload = function () {
          canvas.width = img.width;
          canvas.height = img.height;
          ctx.drawImage(img, 0, 0);
          callback();
        };
        img.src = dataUrl;
      },

      suitable() {

        let height = this.wprops.detail.height;
        let width = this.wprops.detail.width;
        if (height > width) {
          this.zoom = Math.floor((this.height.replace(/px/gi, "") * 1 - 400) / height * 100);
        } else {
          this.zoom = Math.floor((this.width.replace(/px/gi, "") * 1 - 400) / width * 100);
        }

        this.move(50, 50)

      },

      zooma(val) {
        this.transition = "all 200ms";
        this.zoom = val > 100 ? 100 : (val < 1 ? 1 : val.toFixed(2));
        this.timeout.zoom && clearTimeout(this.timeout.zoom);
        this.timeout.zoom = setTimeout(() => {
          this.transition = null;
          clearTimeout(this.timeout.zoom);
          this.timeout.zoom = null;
        }, 200)
      },

      move(x, y) {
        let canvas = document.querySelector("#pic_canvas");
        const detail = {
          height: this.zoom / 100 * this.wprops.detail.height,
          width: this.zoom / 100 * this.wprops.detail.width,
        };
        const width = this.width.replace(/px/gi, "") * 1;
        const height = this.height.replace(/px/gi, "") * 1;
        if (canvas)
          canvas.style.margin = `${height * (y / 100) - detail.height * 0.5}px 0px 0px ${width * (x / 100) - detail.width * 0.5}px`;
        this.info.move = {x: x, y: y};
      },

      line(a, b) {
        const zoom = this.zoom / 100;
        const p1 = {
          x: a.x / zoom,
          y: a.y / zoom
        };
        const p2 = {
          x: b.x / zoom,
          y: b.y / zoom
        };
        this.drawLine(p1, p2);
      },

      hLine(a, x) {
        const zoom = this.zoom / 100;
        const p1 = {
          x: a.x / zoom,
          y: a.y / zoom
        };
        const p2 = {
          x: x / zoom,
          y: a.y / zoom
        };
        this.drawLine(p1, p2);
      },

      vLine(a, y) {
        const zoom = this.zoom / 100;
        const p1 = {
          x: a.x / zoom,
          y: a.y / zoom
        };
        const p2 = {
          x: a.x / zoom,
          y: y / zoom
        };
        this.drawLine(p1, p2);
      },

      pen(point) {
        const zoom = this.zoom / 100;
        const p = {
          x: point.x / zoom,
          y: point.y / zoom
        };
        this.drawPath(p);
      },

      rect(a, b) {
        const zoom = this.zoom / 100;
        const x = a.x / zoom;
        const y = a.y / zoom;
        const w = (b.x - a.x) / zoom;
        const h = (b.y - a.y) / zoom;
        this.drawRect(x, y, w, h);
      },

      ellipse(a, b) {
        const zoom = this.zoom / 100;
        const x = (a.x > b.x ? b.x : a.x) / zoom;
        const y = (a.y > b.y ? b.y : a.y) / zoom;
        const w = (b.x > a.x ? b.x - a.x : a.x - b.x) / zoom;
        const h = (b.y > a.y ? b.y - a.y : a.y - b.y) / zoom;
        this.drawEllipse(x, y, w, h);
      },



      putImage(img, x, y) {
        let canvas = document.querySelector("#pic_canvas");
        let ctx = canvas.getContext('2d');
        ctx.putImageData(img, x, y);
      },

      drawLine(a, b) {
        let canvas = document.querySelector("#pic_canvas");
        let ctx = canvas.getContext('2d');
        ctx.lineCap = "round";
        ctx.lineJoin = "round";
        ctx.beginPath();
        ctx.strokeStyle = this.color;
        ctx.lineWidth = this.pencil;
        ctx.moveTo(a.x, a.y);
        ctx.lineTo(b.x, b.y);
        ctx.stroke();
        ctx.closePath();
      },

      startDrawPath(p) {
        let canvas = document.querySelector("#pic_canvas");
        let ctx = canvas.getContext('2d');
        ctx.lineCap = "round";
        ctx.lineJoin = "round";
        ctx.beginPath();
        ctx.strokeStyle = this.color;
        ctx.lineWidth = this.pencil;
        ctx.imageSmoothingEnabled = true;
        ctx.translate(0.5, 0.5);
        ctx.moveTo(p.x, p.y);
        this.lastPoint = p;
      },

      endDrawPath() {
        let canvas = document.querySelector("#pic_canvas");
        let ctx = canvas.getContext('2d');
        ctx.closePath();
        this.lastPoint = null;
      },

      drawPath(p) {
        if (this.lastPoint !== null) {
          let canvas = document.querySelector("#pic_canvas");
          let ctx = canvas.getContext('2d');
          ctx.lineCap = "round";
          ctx.lineJoin = "round";
          const x = this.lastPoint.x;
          const y = this.lastPoint.y;
          ctx.quadraticCurveTo(x,y,(p.x + x) / 2, (p.y + y) / 2);
          ctx.stroke();
        }
        this.lastPoint = p;
      },

      drawRect(x, y, w, h) {
        let canvas = document.querySelector("#pic_canvas");
        let ctx = canvas.getContext('2d');
        ctx.lineCap = "butt";
        ctx.lineJoin = "miter";
        ctx.strokeStyle = this.color;
        ctx.lineWidth = this.pencil;
        ctx.strokeRect(x, y, w, h);
      },

      drawEllipse(x, y, a, b) {
        let canvas = document.querySelector("#pic_canvas");
        let ctx = canvas.getContext('2d');
        ctx.strokeStyle = this.color;
        ctx.lineWidth = this.pencil;
        const r = (a > b) ? b : a;
        const ratioX = a / r;
        const ratioY = b / r;
        ctx.save();
        ctx.scale(ratioX, ratioY);
        ctx.beginPath();
        ctx.arc((x + a * 0.5) / ratioX, (y + b * 0.5) / ratioY, r * 0.5, 0, 2 * Math.PI, false);
        ctx.closePath();
        ctx.restore();
        ctx.stroke();
      }

    }
  }
</script>

<style scoped lang="scss">

  $color: #91B3B0;

  $blue-dark-lv1: #262626;
  $blue-dark-lv2: #2c2f36;
  $blue-dark-lv3: #44505e;

  $white-alpha-1: rgba(255,255,255,0.1);

  $black-alpha-1: rgba(0,0,0,0.1);

  //
  $box-shadow-color: #333;
  $footer-bg: $blue-dark-lv1;

  $tool-btn-width: 34px;

  .pic-edit {
    font-size: 12px;
    width: 100%;
    height: 100%;
    border: 1px solid $blue-dark-lv1;
    box-sizing: border-box;
    background-color: $blue-dark-lv2;
    display: flex;

    &-panel {
      width: calc(#{$tool-btn-width} * 3);
      background-color: $black-alpha-1;
      display: flex;
      flex-direction: column;

      &-option {
        height: auto;
      }

      &-history {
        flex: 1;
        display: flex;
        flex-direction: column;
        overflow: hidden;
      }

      &-tools {
        height: auto;
        padding: 10px 0;
      }

      &-tool {
        transition: all 300ms;
        display: inline-block;
        width: calc(#{$tool-btn-width} - 2px);
        height: calc(#{$tool-btn-width} - 2px);
        font-size: calc(#{$tool-btn-width} * 0.5);
        line-height: calc(#{$tool-btn-width} - 2px);
        box-sizing: border-box;
        text-align: center;
        cursor: pointer;
        margin: 1px;
        border: 1px solid $white-alpha-1;

        &-hide {
          opacity: 0;
        }

        &:hover, &-active {
          background-color: $white-alpha-1;
        }

      }


    }

    &-container {
      flex: 1;
      overflow: auto;
      table-layout: fixed;
      word-wrap: break-word;
    }

    &-canvas {
      height: 100%;
      width: 100%;
      overflow: hidden;
      position: relative;

    }

  }

  .color-list-item {
    transition: all 400ms;
    display: inline-block;
    width: calc((#{$tool-btn-width} * 3 - 10px) / 4 - 2px);
